function VideoContainer() {
  const videoRef = useRef<HTMLVideoElement>(null);
  const [isPlaying, setIsPlaying] = useState(false);
  
  const togglePlay = () => {
    if (!videoRef.current) return;
    
    if (videoRef.current.paused) {
      videoRef.current.play();
      setIsPlaying(true);
    } else {
      videoRef.current.pause();
      setIsPlaying(false);
    }
  };
  
  return (
    <div className="relative w-full h-full">
      {/* SVG for custom curved shape */}
      <svg 
        className="absolute inset-0 w-full h-full" 
        viewBox="0 0 809 599" 
        preserveAspectRatio="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <defs>
          <clipPath id="videoClipPath">
            <path d="
              M 0,0 
              L 809,0 
              L 809,599 
              L 150,599 
              Q 0,599 0,449
              L 0,0
              Z
            " fill="white"/>
          </clipPath>
        </defs>
        
        {/* Border */}
        <path 
          d="
            M 0,0 
            L 809,0 
            L 809,599 
            L 150,599 
            Q 0,599 0,449
            L 0,0
            Z
          " 
          fill="none" 
          stroke="black" 
          strokeWidth="1"
        />
        
        {/* Video container */}
        <foreignObject 
          x="0" 
          y="0" 
          width="809" 
          height="599" 
          clipPath="url(#videoClipPath)"
        >
          <div className="w-full h-full bg-[#D9D9D9]">
            <video
              ref={videoRef}
              className="w-full h-full object-cover"
              poster="/images/home/video-poster.jpg"
              onClick={togglePlay}
              playsInline
              muted
            >
              <source src="/videos/hero-video.mp4" type="video/mp4" />
            </video>
          </div>
        </foreignObject>
      </svg>
      
      {/* Play button - centered in video */}
      {!isPlaying && (
        <button
          onClick={togglePlay}
          aria-label="Play video"
          className="absolute w-[115px] h-[115px] rounded-full bg-white/90 flex items-center justify-center hover:bg-white transition-colors shadow-lg"
          style={{
            left: '347px',
            top: '242px'
          }}
        >
          <svg width="40" height="40" viewBox="0 0 24 24" fill="none">
            <path d="M8 5v14l11-7L8 5z" fill="black" />
          </svg>
        </button>
      )}
    </div>
  );
}